<template>
  <div class="manage">
    <!-- :visible指的是属性绑定，表示弹框的显示隐藏，值为true显示
    .sync指同步动态双向的来表示visible的值，当弹窗发生变化同步修改visible的值  -->
    <!-- 弹窗 -->
    <el-dialog
      :title="opretateType === 'add' ? '新增用户' : '更新用户'"
      :visible.sync="isShow"
    >
      <common-form
        :formLabel="operateFormLabel"
        :form="operateForm"
        :inline="true"
        ref="form"
      ></common-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false">取消</el-button>
        <el-button type="primary" @click="confirm">确定</el-button>
      </div>
    </el-dialog>

    <!-- 头部 -->
    <div class="manage-header">
      <el-button type="primary" @click="addUser">新增</el-button>
      <div class="search">
        <common-form :formLabel="formLabel" :form="searchForm" :inline="true">
        </common-form>
        <el-button type="primary" @click="getList(searchForm.keyword)"
          >搜索</el-button
        >
      </div>
    </div>
    <!-- 尾部 -->
    <div class="manage-footer">
      <common-table
        :tableLabel="tableLabel"
        :tableData="tableData"
        :config="config"
        @changePage="getList()"
        @edit="editUser"
        @del="delUser"
      ></common-table>
    </div>
  </div>
</template>

<script>
import CommonForm from "../../src/components/commonForm.vue";
import CommonTable from "../../src/components/commonTable.vue";
import { getUser } from "../../api/data.js";
export default {
  name: "MallName",
  data() {
    return {
      opretateType: "add",
      isShow: false,
      operateFormLabel: [
        {
          model: "name",
          label: "姓名",
          type: "input",
        },
        {
          model: "age",
          label: "年龄",
          type: "input",
        },
        {
          model: "sex",
          label: "性别",
          type: "select",
          opts: [
            {
              label: "男",
              value: 1,
            },
            {
              label: "女",
              value: 0,
            },
          ],
        },
        {
          model: "birth",
          label: "出生日期",
          type: "date",
        },
        {
          model: "addr",
          label: "地址",
          type: "input",
        },
      ],
      operateForm: {
        name: "",
        addr: "",
        age: "",
        birth: "",
        sex: "",
      },
      formLabel: [{ model: "keyword", label: "", type: "input" }],
      searchForm: [{ keyword: "" }],
      tableLabel: [
        {
          prop: "name",
          label: "姓名",
        },
        {
          prop: "age",
          label: "年龄",
        },
        {
          prop: "sexLabel",
          label: "性别",
        },
        {
          prop: "birth",
          label: "出生日期",
          width: 200,
        },
        {
          prop: "addr",
          label: "地址",
          width: 320,
        },
      ],
      tableData: [
        {
          name: "十五哟",
          addr: "蝶毒",
          age: "12",
          birth: "1",
          sex: "1",
        },
      ],
      config: { page: 1, total: 30 },
    };
  },
  methods: {
    //判断编辑还是新增
    confirm() {
      if (this.opretateType === "edit") {
        this.$http.post("/user/edit", this.operateForm).then((res) => {
          console.log(res);
          this.isShow = false;
          this.getList();
        });
      } else {
        this.$http.post("/user/add", this.operateForm).then((res) => {
          this.isShow = false;
          this.tableData.push(res);
          this.getList();
          console.log(res);
          console.log(this.tableData);
        });
      }
    },
    addUser() {
      this.isShow = true;
      this.opretateType = "add";
      this.operateForm = {
        name: "",
        age: "",
        birth: "",
        addr: "",
        sex: "",
      };
    },
    editUser(row) {
      console.log("编辑用户");
      this.opretateType = "edit";
      this.isShow = true;
      this.operateForm = row;
    },
    delUser(row) {
      this.$confirm("此操作将永久删除文件，是否继续", "提示", {
        confirmButtonText: "确认",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          const id = row.id;
          this.$http
            .post("user/del", {
              params: { id },
            })
            .then(() => {
              this.$message({
                type: "success",
                message: "删除成功",
              });
              this.getList();
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    getList(name = "") {
      this.config.loading = true;
      name ? (this.config.page = 1) : "";
      getUser({ page: this.config.page, name }).then((res) => {
        this.tableData = res.list.map((item) => {
          item.sexLabel = item.sex === 0 ? "女" : "男";
          return item;
        });

        this.config.total = res.count;
        this.config.loading = false;
      });
    },
  },
  created() {
    this.getList();
  },
  components: { CommonForm, CommonTable },
};
</script>

<style scoped>
.manage-header {
  display: flex;
  justify-content: space-between;
  padding-bottom: 20px;
  height: 30px;
}
.search {
  display: flex;
  justify-content: space-between;
  width: 250px;
  height: 100%;
}
</style>