<template>
  <div>
    <el-table :data="tableData" style="width: 85%">
      <el-table-column
        show-overflow-tooltip
        v-for="item in tableLabel"
        :key="item.prop"
        :label="item.label"
        :width="item.width ? item.width : 125"
      >
        <!-- 插槽 -->
        <template slot-scope="scope">
          <!-- 查看显示后端返回的字段并以文字渲染到页面上 -->
          <span style="margin-left: 10px">{{ scope.row[item.prop] }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="180">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.row)"
            >删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      class="paper"
      layout="prev, pager, next"
      :total="config.total"
      :current-page.sync="configg.page"
      @current-change="changePage"
      :page-size="20"
    >
    </el-pagination>
  </div>
</template>

<script>
export default {
  props: { tableData: Array, tableLabel: Array, config: Object },
  methods: {
    //触发父组件自定义事件
    handleEdit(row) {
      this.$emit("edit", row);
    },
    handleDelete(row) {
      this.$emit("del", row);
    },
    changePage(page) {
      this.$emit("changePage", page);
    },
  },
  computed: {
    configg() {
      return this.config;
    },
  },
};
</script>

<style  scoped>
.common-table {
  height: calc(100%-62px);
  background-color: #fff;
  position: relative;
}
.paper {
  position: absolute;
  bottom: 0;
  right: 20px;
}
</style>