<template>
  <el-form ref="form" label-width="100px" :inline="true">
    <el-form-item
      v-for="item in formLabel"
      :key="item.label"
      :label="item.label"
    >
      <!-- 不可以直接将 prop 的变量应用于子组件深层次组件的 v-model 上
      （因为 v-model 会隐含的对 prop 值更新） -->
      <el-input
        :placeholder="'请输入' + item.label"
        v-if="item.type === 'input'"
        v-model="formData[item.model]"
      ></el-input>

      <el-switch
        v-if="item.type === 'switch'"
        v-model="formData[item.model]"
      ></el-switch>
      <el-date-picker
        v-if="item.type === 'date'"
        type="date"
        placeholder="选择日期"
        value-format="yyyy-MM-dd"
        v-model="formData[item.model]"
      >
      </el-date-picker>

      <el-select
        placeholder="请选择"
        v-if="item.type === 'select'"
        v-model="formData[item.model]"
      >
        <el-option
          v-for="item in item.opts"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option
      ></el-select>
    </el-form-item>
    <el-form-item>
      <!-- 如果没有表单项 插入插槽 -->
      <slot name="footer"></slot
    ></el-form-item>
  </el-form>
</template>

<script>
export default {
  name: "commonForm",
  props: {
    formLabel: [],
    form: {},
    inline: Boolean,
  },
  computed: {
    formData() {
      return this.form;
    },
  },
};
</script>

